@import '_base.sass';

//Mobile up to 1000px
@media only screen and (max-width: 1000px) {
    header {
        @include theme($theme: $color-theme-Darkgreen);
        display: flex;
        flex-flow: row;
        width: 100vw;
        padding: 4vh 5vw 4vh 5vw;
        nav {
            ul {
                //display: flex
                //flex-flow: row
                li {
                    list-style-type: none;
                    a {
                        text-decoration: none; }
                    a:link {
                        color: $color-Darkgreen-font; }
                    a:visited {
                        color: $color-Darkgreen-font; }
                    a:hover {
                        color: #7A9983; } } } }
        .hamburger {
            position: fixed;
            z-index: 5000;
            border: none;
            hight: 3rem;
            width: 3rem;
            float: rigth; }
        .closeOpt {
            display: none; }
        .navMenu {
            position: fixed;
            transform: transLateY(-100%);
            transition: transform 0.2s;
            top: 0;
            left: 0;
            rigth: 0;
            bottom: 0;
            z-index: 4999;
            .menuItem {
                display: block; } }
        //JS class
        .menuVisability {
            transform: transLateY(0); } } }

